<template>
	<div class="body">
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span> 
					<span class="name">投资</span>
				</div>
			</div>
			<!--新增投资模块-->
			<div class="table" v-if="is_add_page == 1">
				<el-table :data="add_info" border style="width: 100%">
					<el-table-column prop="fund_type" label="投资标的类别" width="180">
						<template slot-scope="scope">
							<el-select v-model="scope.row.fund_type" @change="chooseFundType" clearable placeholder="请选择">
							    <el-option v-for="item in fund_type_list" :key="item.type" :label="item.name" :value="item.type">
							    </el-option>
							</el-select>
						</template>
					</el-table-column>
					<el-table-column prop="fund_id" label="投资标的名称" width="180">
						<template slot-scope="scope">
							<el-select v-model="scope.row.fund_id" @change="chooseFund" filterable clearable placeholder="请选择">
							    <el-option v-for="item in target_list" :key="item.fund_id" :label="item.name" :value="item.fund_id">
							    </el-option>
							</el-select>
						</template>
					</el-table-column>
					<el-table-column prop="invest_date" label="出资日期" width="180">
						<template slot-scope="scope">
							<el-date-picker v-model="scope.row.invest_date" @change="getEventCountData" type="date"  :editable='false' value-format="yyyy-MM-dd" placeholder="选择日期">
							</el-date-picker>
						</template>
					</el-table-column>
					<el-table-column prop="event" label="事件" width="300">
					</el-table-column>
					<el-table-column prop="should_fee" label="当期认缴额（万元）" width="180">
						<template slot-scope="scope">
							<el-input placeholder="请输入内容" v-model="scope.row.should_fee" clearable maxlength='16'>
							</el-input>
						</template>
					</el-table-column>
					<el-table-column prop="fee" label="当期投资额（万元）" width="180">
						<template slot-scope="scope">
							<el-input placeholder="请输入内容" v-model="scope.row.fee" clearable maxlength='16'>
							</el-input>
						</template>
					</el-table-column>
					<el-table-column prop="unit" label="币种" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.unit">
								{{ scope.row.unit == 1?'人民币':'美元' }}
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="note" label="备注" width="180">
						<template slot-scope="scope">
							<el-input placeholder="请输入内容" v-model="scope.row.note" clearable>
							</el-input>
						</template>
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="120">
						<template slot-scope="scope">
							<el-button  type="text" size="small" @click="addSave(scope.row)">保存</el-button>
							<el-button  type="text" size="small" @click="editCancel(scope.row)">取消</el-button>
						</template>
					</el-table-column>
				</el-table>
			</div>
			
			
			<!--查看投资模块-->
			<div class="table" v-else>
				<el-table :data="tableData" border style="width: 100%">
					<el-table-column prop="event" label="投资标的类别" width="180">
						<template slot-scope="scope">
							{{ scope.row.type_name }}
						</template>
					</el-table-column>
					<el-table-column prop="output" @change="chooseFund" label="投资标的名称" width="180">
						<template slot-scope="scope">
							{{ scope.row.name }}
						</template>
					</el-table-column>
					<el-table-column prop="date" label="出资日期" width="180">
						<template slot-scope="scope">
							<div class="" v-if="is_editable">
								<el-date-picker v-model="edit_invest_date" @change="getEventCountData" type="date" :editable='false' value-format="yyyy-MM-dd" placeholder="选择日期">
								</el-date-picker>
							</div>
							<div class="" v-else>
								{{ scope.row.invest_date }}
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="output" label="事件" width="250">
						<template slot-scope="scope">
							<div class="" v-if="is_editable">
								{{ edit_event }}
							</div>
							<div class="" v-else>
								{{ scope.row.event }}
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="output" label="当期认缴额" width="180">
						<template slot-scope="scope">
							<div class="" v-if="is_editable">
								<el-input placeholder="请输入内容" v-model="edit_should_fee" clearable maxlength='16'>
								</el-input>
							</div>
							<div class="" v-else>
								{{ scope.row.should_fee }}
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="output" label="当期投资额" width="180">
						<template slot-scope="scope">
							<div class="" v-if="is_editable">
								<el-input placeholder="请输入内容" v-model="edit_fee" clearable maxlength='16'>
								</el-input>
							</div>
							<div class="" v-else>
								{{ scope.row.fee }}
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="unit" label="币种" width="180">
						<template slot-scope="scope">
							<div class="" v-if="is_editable">
								{{ edit_unit == 1?'人民币':'美元' }}
							</div>
							<div class="" v-else>
								{{ scope.row.unit == 1?'人民币':'美元' }}
							</div>
						</template>
					</el-table-column>
					<el-table-column prop="output" label="备注" width="180">
						<template slot-scope="scope">
							<div class="" v-if="is_editable">
								<el-input placeholder="请输入内容" v-model="edit_note" clearable>
								</el-input>
							</div>
							<div class="" v-else>
								{{ scope.row.note }}
							</div>
						</template>
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="120" v-if="is_delete">
						<template slot-scope="scope">
							<el-button v-if="is_editable" type="text" size="small" @click="editSave(scope.row)">保存</el-button>
							<el-button v-if="is_editable" type="text" size="small" @click="editCancel(scope.row)">取消</el-button>
							<el-button v-if="!is_editable" type="text" size="small" @click="edit(scope.row)">编辑</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
			</div>
			
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn,isValidMoneyLength,getSelectedItem } from '../../config/mUtils'
	import { getInvestAddInfoMotherCashApi,addInvestMotherCashApi,getInvestMotherCashApi,editInvestMotherCashApi,getEventByDateApi } from '../../config/api'
	
	export default {
		data() {
			return {
				id: '',
				type: 1,//1代表母基金模块；2代表自管基金模块
				is_delete: 1,//列表页传过来的参数代表是否可编辑（可删除即可编辑）
				add_info: [
					{
						fund_id: '',
						fund_type: '',
						event: '',
						invest_date: '',
						fee: '',
						should_fee: '',
						note: '',
						unit: '',
						event_count: '',
					}
				],
				tableData: [ ],
				// 编辑时数据开始
				edit_fund_type: '',
				edit_fund: '',
				edit_event: '',
				edit_invest_date: '',
				edit_should_fee: '',
				edit_fee: '',
				edit_unit: '',
				edit_note: '',
				edit_event_count: '',
				// 编辑时数据结束
				coin_list: [
					{
						label: '人民币',
						value: 1
					},
					{
						label: '美元',
						value: 2
					}
				],
				is_add_page: 1,
				is_editable: 0,
				fund_type_list: [],
				target_list: [],
				all_target_list: [],
			}
		},
		created() {
			this.is_add_page = this.$route.query.is_add_page;
			this.is_delete = this.$route.query.is_delete;
			this.id = this.$route.query.id;
			this.type = this.$route.query.type;
			if(!this.is_add_page){
				this.count = this.$route.query.count;
//				this.getAddInfo()
				this.getInitData();
			} else {
				this.getAddInfo()
			}
		},
		computed: {
	
		},
		mounted() {},
		methods: {
			getAddInfo(){
				getInvestAddInfoMotherCashApi({
					fof_id: this.id,
					difference_sign: this.type
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.fund_type_list = res.data.payload.type;
						this.all_target_list = res.data.payload.data;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			addSave(){
				if(isValidMoneyLength(parseInt(this.add_info[0].should_fee))){
					dialogConfirm('info','当期认缴金额过大!');
					return;
				}
				if(isValidMoneyLength(parseInt(this.add_info[0].fee))){
					dialogConfirm('info','当期投资金额过大!');
					return;
				}

				if(parseFloat(this.add_info[0].should_fee) <  parseFloat(this.add_info[0].fee) ){
					dialogConfirm('info','当期投资金额不能大于当期认缴额!');
					return;
				}
				addInvestMotherCashApi({
					fof_id: this.id,
					fund_type: this.add_info[0].fund_type,
					fund_id: this.add_info[0].fund_id,
					event: this.add_info[0].event,
					invest_date: this.add_info[0].invest_date,
					fee: this.add_info[0].fee,
					should_fee: this.add_info[0].should_fee,
					note: this.add_info[0].note,
					difference_sign: this.type,
					event_count: this.add_info[0].event_count
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.clearData()
						this.addCancel()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			addCancel(){
				if (this.type == 1) {
					var name = 'motherFunRunDataCashFlowHome';
				} else{
					var name = 'selfAdministerFunRunDataCashFlowHome';
				}
				this.$router.push({
					name: name,
					query: {
						id: this.id
					}
				})
			},
			// 查看模块开始
			//获取自管基金，母基金详情
			getInitData(){
				 getInvestMotherCashApi({
				 	count: this.count,
				 	difference_sign: this.type
				 }).then(res => {
				 	if(res.data.msgNo == 1) {
				 		this.$set(this.tableData,0,res.data.payload.data);
				 	} else {
				 		dialogConfirm("info", res.data.message);
				 	}
				 })
			},
			edit(e){
				this.is_editable = 1;
				this.getTargetList(this.tableData[0].fund_type)
				this.makeData(e)
			},
			editSave(e){
				if(isValidMoneyLength(parseInt(this.edit_should_fee))){
					dialogConfirm('info','当期认缴金额过大!');
					return;
				}
				if(isValidMoneyLength(parseInt(this.edit_fee))){
					dialogConfirm('info','当期投资金额过大!');
					return;
				}
				if(parseFloat(this.edit_should_fee) <  parseFloat(this.edit_fee) ){
					dialogConfirm('info','当期投资额不能大于当期认缴额!');
					return;
				}
				editInvestMotherCashApi({
					id: e.id,
					fof_id: this.id,
					fund_id: this.edit_fund,
					fund_type: this.edit_fund_type,
					event: this.edit_event,
					invest_date: this.edit_invest_date,
					fee: this.edit_fee,
					should_fee: this.edit_should_fee,
					note: this.edit_note,
				 	difference_sign: this.type,
				 	event_count: this.edit_event_count
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.is_editable = false;
						this.getInitData()
						//还需要请求数据接口刷新页面
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			editCancel(){
				this.is_editable = 0;
				this.clearData()
			},
			makeData(e){
				this.edit_fund_type = e.fund_type;
				this.edit_fund = e.fund_id;
				this.edit_event = e.event;
				this.edit_invest_date = e.invest_date;
				this.edit_fee = e.fee;
				this.edit_should_fee = e.should_fee;
				this.edit_unit = e.unit;
				this.edit_note = e.note;
			},
			clearData(){
				this.edit_fund_type = '';
				this.edit_fund = '';
				this.edit_event = '';
				this.edit_invest_date = '';
				this.edit_fee = '';
				this.edit_should_fee = '';
				this.edit_unit = '';
				this.edit_note = '';
				this.edit_event_count = '';

				this.add_info[0].fund_id = '';
				this.add_info[0].fund_type = '';
				this.add_info[0].event = '';
				this.add_info[0].invest_date = '';
				this.add_info[0].fee = '';
				this.add_info[0].should_fee = '';
				this.add_info[0].note = '';
				this.add_info[0].event_count = '';
			},

			
			chooseFundType(e){
				this.add_info[0].fund_id = '';
				this.getTargetList(e)
			},
			chooseFund(e){
				if (e) {
					var item = getSelectedItem(this.target_list,'fund_id',e);
					if (this.is_add_page) {
						this.add_info[0].unit = item.unit;
					} else{
						this.edit_unit = item.unit;
					}
					this.getEventCountData()
				} else {
					this.add_info[0].unit = '';
					this.edit_unit = '';
					this.edit_event = '';
					this.add_info[0].event = '';
				}
			},
			getEventCountData(){
				if (this.is_add_page) {
					if (this.add_info[0].fund_type == 1) {
						var fund_type = 2;
					} else if (this.add_info[0].fund_type == 2){
						var fund_type = 3;
					} else if (this.add_info[0].fund_type == 3){
						var fund_type = 5;
					} else if (this.add_info[0].fund_type == 4){
						var fund_type = 4;
					} else {
						var fund_type = '';
					}
					var date = this.add_info[0].invest_date;
					var fund_id = this.add_info[0].fund_id;
					var event_id = '';
				} else {
					if (this.edit_fund_type == 1) {
						var fund_type = 2;
					} else if (this.edit_fund_type == 2){
						var fund_type = 3;
					} else if (this.edit_fund_type == 3){
						var fund_type = 5;
					} else if (this.edit_fund_type == 4){
						var fund_type = 4;
					} else {
						var fund_type = '';
					}
					var date = this.edit_invest_date;
					var fund_id = this.edit_fund;
					var event_id = this.tableData[0].id;
				}
				if (this.type == 1) {
					var type = 2;
				} else {
					var type = 3;
				}
				getEventByDateApi({
					type: type,
					id: this.id,
					cash_type: 2,
					date: date,
					fund_type: fund_type,
					fund_id: fund_id,
					event_id: event_id
				}).then(res => {
					if(res.data.msgNo == 1) {
						if (this.is_add_page) {
							this.add_info[0].event = res.data.payload.event_name;
							this.add_info[0].event_count = res.data.payload.event_count;
						} else{
							this.edit_event = res.data.payload.event_name;
							this.edit_event_count = res.data.payload.event_count;
						}
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			
			
			getTargetList(e){
				var arr = [];
				for (var i = 0,max = this.all_target_list.length;i<max;i++) {
					if (this.all_target_list[i].type == e) {
						arr.push(this.all_target_list[i]);
					}
				}
				this.target_list = arr;
			},
			getChoosedItem(arr,pro,id){
				for (var i = 0,max = arr.length;i<max;i++) {
					if (arr[i][pro] == id) {
						return arr[i]
					}
				}
			},
			
		}
	}
</script>

<style lang="scss" scoped>
	.body {
		padding: 0 0px 30px 0px;
	}
	
	
	.cash_flow_table_title {
		font-size: 14px;
		color: #6292F4;
		margin: 22px 0px;
		.ts {
			color: #FF3B30;
			margin-left: 23px;
		}
		.name {
			color: #6292F4;
		}
	}
	
	.circle_blue {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 5px;
		margin-left: 8px;
		background-color: #6292F4;
	}
</style>


